body {
  margin: 0 auto;
  padding: 0;
  max-width: 800px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  padding: 2%;
}

.Header {
  display: -webkit-flex;
  display: flex;
}

.HeaderText {
  -webkit-align-self: center;
          align-self: center;
}

img {
  -webkit-flex-basis: 30%;
          flex-basis: 30%;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  -webkit-align-self: center;
          align-self: center;
}

.Title {
  font-size: 1.5em;
}

.mediaPlayer {
  height: 40px;
  width: 100%;
  margin: 2vh 0;
}
.Podcast{
  cursor: pointer;
  text-align: left;
  padding: 1%;
}

.PodDesc{
  font-size: .8em;
}

.Selected {
  border: 2px solid black;
  border-radius: 5px;
  background: #ddd;
}
